<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <li class="cart__order">
    <h3>{{ item.product.title }}</h3>
    <b v-if="item.amount">{{ amountPriceFormat }} ₽</b>
    <b v-if="item.quantity">{{ quantityPriceFormat }} ₽</b>
    <span>Артикул: {{ item.product.id }}</span>
  </li>
</template>

<script>
import numberFormat from '@/halpers/numberFormat';

export default {
  props: ['item'],
  computed: {
    amountPriceFormat() {
      return numberFormat(this.item.amount * this.item.product.price);
    },
    quantityPriceFormat() {
      return numberFormat(this.item.quantity * this.item.product.price);
    },
  },
};
</script>
